import { Button } from 'antd-mobile';
import React from 'react';
import { useHistory } from 'react-router-dom';
import { ButtonBoxInner, Page, ButtonBox } from '../../styles';
import { ExamMainInner, ExamInfoTitle, ExamMain } from '../styles';

const Info: React.FC = () => {
  const history = useHistory();
  return (
    <Page>
      <ExamInfoTitle>考试信息</ExamInfoTitle>
      <ExamMain>
        <ExamMainInner>
          种类 ：中心考试
          <br />
          科目 ：数学
          <br />
          年份 ：2020年6月
          <br />
          总分 ：200
          <br />
          时间 ：120分钟
          <br />
          题数 ：4
          <br />
          上次记录 ：-
          <br />
        </ExamMainInner>
      </ExamMain>
      <ButtonBox>
        <ButtonBoxInner>
          <Button type="primary" onClick={(): void => history.push('/exam')}>
            开始模拟
          </Button>
        </ButtonBoxInner>
      </ButtonBox>
    </Page>
  );
};

export default Info;
